import React from 'react';
import styled from 'styled-components';

const Input = styled.input`
    padding: 0.4em 0.8em;
    font-family: inherit;
    margin: 0.5em;
    width: 18em;
    font-size: 0.9em;

    /* &:is([type="email"], [type="text"], [type="password"], [type="submit"]):focus {
        outline: none;
        box-shadow: 0 0 4px 2px ${({ theme }) => theme.primary};
    } */
`

const FormButtonRow = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    & > input {
        width: initial;
        cursor: pointer;
    }

    & > input:only-child {
        margin: 0.5em 0.5em 0.5em auto;
    }
`

const PopupOverlay = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    min-width: 100vw;
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
    background: rgba(25,25,25,0.8);
`

const PopupBox = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    min-width: 16em;
    max-width: 30em;
    min-height: 8em;
    padding: 0.5em;
    overflow: visible;
    background: ${({ theme }) => theme.body};
    border: 2px solid ${({ theme }) => theme.text};
    border-radius: 4px;
    box-shadow: 0px 2px 4px ${({ theme }) => theme.text};

    & > p {
        margin: 1em;
    }
`

export default function Popup({ text = "", dismissClick, dismissText, actionClick, actionText }) {
    return (
        <PopupOverlay>
            <PopupBox>
                <p>{text}</p>
                <FormButtonRow>
                    {dismissText && dismissClick
                        ? <Input type="submit" value={dismissText} onClick={() => dismissClick()} />
                        : null
                    }
                    {actionText && actionClick
                        ? <Input type="submit" value={actionText} onClick={() => actionClick()} />
                        : null
                    }
                </FormButtonRow>
            </PopupBox>
        </PopupOverlay>
    )
}
