export function get_deck() {
    // define cards
    let deck = [],
        cards = ['01HR','02HR','03HR','04HR','05HR','06HR','07HR',
                 '08HR','09HR','10HR','11HR','12HR','13HR',
                 '01SB','02SB','03SB','04SB','05SB','06SB','07SB',
                 '08SB','09SB','10SB','11SB','12SB','13SB',
                 '01DR','02DR','03DR','04DR','05DR','06DR','07DR',
                 '08DR','09DR','10DR','11DR','12DR','13DR',
                 '01CB','02CB','03CB','04CB','05CB','06CB','07CB',
                 '08CB','09CB','10CB','11CB','12CB','13CB'
                ];
    
    // randomly pull cards and add to deck
    while(cards.length) {
        let index = randint(cards.length),
            chosen = cards[index];
        deck.push(chosen);
        cards.splice(index,1);
    }

    // add blanks at first column
    deck = [].concat('01XX', deck.slice(0));
    deck = [].concat(deck.slice(0,14), '02XX', deck.slice(14));
    deck = [].concat(deck.slice(0,28), '03XX', deck.slice(28));
    deck = [].concat(deck.slice(0,42), '04XX', deck.slice(42));

    // return deck
    if(deck.length === 56) {
        return deck;
    } else {
        return false;
    }
}

export function get_rowcol_from_index(index) {
    return {row: Math.floor(index/14), col: index%14};
}

export function get_index_from_rowcol(row, col) {
    return row*14+col;
}

export function randint(n) {
    return Math.floor(Math.random()*n);
}

export function get_deck_with_swap({deck, card1, card2}) {
    const index1 = deck.indexOf(card1);
    const index2 = deck.indexOf(card2);
    deck[index1] = card2;
    deck[index2] = card1;
    return [...deck];
}

export function get_next_round_deck(deck) {
    let pile = [];

    // add cards out of pattern to pile
    for(let row=0; row<4; row++) {
        let broken = false;
        for(let col=0; col<14; col++) {
            const index = get_index_from_rowcol(row, col);
            const value = Number(deck[index].slice(0,2));
            const suit = deck[index].slice(2);
            const rowSuit = deck[get_index_from_rowcol(row,0)].slice(2);
            if(value !== col+1 || suit !== rowSuit || broken) {
                broken = true;
                if(suit !== 'XX') {
                    pile.push(deck[index]);
                }
                deck[index] = '00XX';
            }
        }
    }

    // shuffle pile
    pile = shuffle(pile);

    // deal pile to appropriate spots
    for(let row=0; row<4; row++) {
        let dealing = false;
        for(let col=0; col<14; col++) {
            const index = get_index_from_rowcol(row, col);
            const suit = deck[index].slice(2);

            // if it's an empty spot
            if(suit === 'XX') {
                if(dealing) {
                    // if not first spot in row, deal card
                    deck[index] = pile.pop();
                } else {
                    // if first spot, name the blank
                    deck[index] = '0' + String(row+1) + 'XX';
                }
                dealing = true;
            }
        }
    }

    // return deck
    if(pile.length) {
        return false;
    } else {
        return deck;
    }
}

export function get_skip_move_deck(deck) {
    // find fillable blank spots
    let options = [];
    for(let index=0; index<deck.length; index++) {
        // if not blank, or is right of King, or is right of blank
        if((deck[index].slice(2) !== 'XX') ||
        (index > 0 && deck[index-1].slice(0,2) === '13') ||
        (index > 0 && deck[index-1].slice(2) === 'XX')) {
            continue;
        }

        options.push(deck[index]);
    }

    // break if no options
    if(!options.length) return deck;

    // choose one at random
    const chosen = options[randint(options.length)];
    const chosenIndex = deck.indexOf(chosen);

    // find eligible card and swap
    let swapped = [];
    const chosenCol = get_rowcol_from_index(deck.indexOf(chosen)).col;
    if(chosenCol === 0) {
        // find aces that are not in left-most column
        const aces = ['01HR', '01SB', '01DR', '01CB'];
        let aceOptions = [];
        for(let a=0; a<4; a++) {
            const {col} = get_rowcol_from_index(deck.indexOf(aces[a]));
            if(col > 0) {
                aceOptions.push(aces[a]);
            }
        }

        // swap ace with blank
        const next = aceOptions[randint(aceOptions.length)];
        swapped = get_deck_with_swap({deck, card1: chosen, card2: next});
    } else {
        // find eligible card
        const left = deck[chosenIndex-1];
        const nextValue = Number(left.slice(0,2))+1;
        const nextSuit = left.slice(2);

        // swap eligible card with blank
        const next = nextValue < 10
            ? '0' + String(nextValue) + nextSuit
            : String(nextValue) + nextSuit;        
        swapped = get_deck_with_swap({deck, card1: chosen, card2: next});
    }

    return swapped;
}

function shuffle(array) {
    let currentIndex = array.length, randomIndex;
  
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
  
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex--);
  
        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
         array[randomIndex], array[currentIndex]];
    }
  
    return array;
}