import React, { useState } from 'react';
import styled from 'styled-components';
import Header from './Header'
import Card from './Card';
import Popup from './Popup';
import {
    get_deck,
    get_rowcol_from_index,
    get_deck_with_swap,
    get_next_round_deck,
    get_skip_move_deck
} from '../logic';

const GameWrapper = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    & > *:nth-child(1) {
        flex-grow: 0;
    }

    & > *:nth-child(2) {
        flex-grow: 1;
    }
`

const GameBoardWrapper = styled.div`
    display: grid;
    grid-template-columns: repeat(14, 1fr);
    grid-template-rows: repeat(4, 1fr);
`

export default function Game() {
    const [deck, setDeck] = useState(get_deck());
    const [selected, setSelected] = useState(null);
    const [popupText, setPopupText] = useState(null);

    const select = (card) => {
        setSelected(card);
        document.getElementById(card).classList.add("selected");
    };

    const unselect = (card) => {
        setSelected(null);
        document.getElementById(card).classList.remove("selected");
    }

    const reshuffle = () => {
        setDeck([...get_next_round_deck(deck)]);
    }

    const skipMove = () => {
        setDeck([...get_skip_move_deck(deck)]);
    }

    const cardClick = (card) => {

        const index = deck.indexOf(card);
        const {row, col} = get_rowcol_from_index(index);
        console.log({card, index, row, col});

        //unselect the same card
        if(card === selected) {
            unselect(selected);
            return;
        }
        
        //select a non-blank when nothing selected
        if(card.slice(2) !== 'XX' && selected === null) {
            select(card);
            return;
        }
        
        //if click on blank with ace selected
        if(card.slice(2) === 'XX' && selected !== null && selected.slice(0,2) === '01') {
            const {col} = get_rowcol_from_index(deck.indexOf(card));
            if(col === 0) {
                unselect(selected);
                setDeck(get_deck_with_swap({deck, card1: card, card2: selected}));
            }
        }

        //if click on blank with any card selected
        if(card.slice(2) === 'XX' && selected !== null) {
            const {col} = get_rowcol_from_index(deck.indexOf(card));
            console.log("column "+col);
            if(col !== 0) {
                const left = deck[deck.indexOf(card)-1];
                console.log("left card is "+left);
                //if left of blank is same suit and one less
                if(left.slice(2) === selected.slice(2) && Number(left.slice(0,2))+1 === Number(selected.slice(0,2))) {
                    console.log("should go there");
                    unselect(selected);
                    setDeck(get_deck_with_swap({deck, card1: card, card2: selected}));
                }
            }
        }
    }

    return (
        <GameWrapper>
            {popupText
                ?
                <Popup
                    text={popupText}
                    dismissClick={() => setPopupText(null)}
                    dismissText="Cancel"
                    actionClick={() => reshuffle()}
                    actionText="Proceed"
                />
                : null
            }
            <Header reshuffle={reshuffle} skipMove={skipMove} />
            <GameBoardWrapper>
                {deck.map((card, i) => <Card key={i} card={card} handleClick={cardClick} />)}
            </GameBoardWrapper>
        </GameWrapper>
        
    );
}