import React from 'react';
import styled from 'styled-components';
import { GiSpades, GiDiamonds, GiHearts, GiClubs } from 'react-icons/gi';

const getTextFromValue = (value) => {
    //convert Ace, Jack, Queen, King to letters
    switch(Number(value)) {
        case 0:
            return '';
        case 1:
            return 'A';
        case 11:
            return 'J';
        case 12:
            return 'Q';
        case 13:
            return 'K';
        default:
            return String(Number(value));
    }
}

const CardBox = styled.div`
    margin: 0.2rem;
    border-radius: 0.6rem;
    border: 0.2rem solid ${({ theme }) => theme.gray};
    padding: 0.4rem;
    display: flex;
    flex-direction: column;
    background-color: ${({ theme }) => theme.cardBg};
    cursor: pointer;
    overflow: hidden;

    &.selected {
        background-color: ${({ theme }) => theme.selected};
    }

    &.selected > .card {
        border-color: ${({ theme }) => theme.gray};
    }
`

const CardWrapper = styled.div`
    border: 0.1rem solid transparent;
    padding: 0.2rem;
    border-radius: 0.4rem;
    background-color: #DDD;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    font-size: 1.6rem;
    font-family: 'Noticia Text', 'Palatino Linotype', Georgia, 'Book Antiqua', Palatino, serif;

    color: ${({ color, theme }) => color === 'R' ? theme.red : color === 'B' ? theme.black : theme.selected};

    & > * {
        margin: 0;
        line-height: 1;
    }
`

export default function Card({ card, handleClick }) {
    const value = card.slice(0,2);
    const suit = card.slice(2,3);
    const color = card.slice(3,4);
    const text = suit === 'X' ? '' : getTextFromValue(value);

    return (
        <CardBox id={card} className="card-box" onClick={() => handleClick(card)}>
            <CardWrapper className="card" color={color}>
                {suit === 'H'
                    ? <GiHearts title="hearts"/>
                    : suit === 'D'
                        ? <GiDiamonds title="diamonds" />
                        : suit === 'S'
                            ? <GiSpades title="spades" />
                            : suit === 'C'
                                ? <GiClubs title="clubs" />
                                : <p></p>
                }
                <p>{text}</p>
            </CardWrapper>
        </CardBox>
    )
}
