import React from 'react';
import styled from 'styled-components';
// import { FiSettings } from 'react-icons/fi';
import Logo from './Logo';
import NextShuffle from './NextShuffle';
import SkipMove from './SkipMove';

const HeaderWrapper = styled.div`
    /* display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 100%;
    height: 2rem;
    & > *:nth-child(0) {
        justify-self: start;
        grid-column: 1;
    }
    & > *:nth-child(0) svg {
        height: 2rem;
    }
    & > *:nth-child(1) {
        justify-self: center;
        grid-column: 2;
    }
    & > *:nth-child(2) {
        justify-self: end;
        grid-column: 3;
    } */

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    & > * {
        flex-basis: 0;
    }
    #logo {
        height: 100%;
        border-radius: 5px;
        border: 1px solid #955C07;
    }
    #next-shuffle, #skip-move {
        cursor: pointer;
    }
`

const SvgWrapper = styled.div`
    margin: auto 0.5rem;
    padding: 0.2rem;
    display: flex;
    justify-content: center;
    align-items: center;

    & > * {
        color: ${({ theme }) => theme.text};
    }
`

export default function Header({ reshuffle, skipMove }) {
    return (
        <HeaderWrapper>
            <SvgWrapper>
                <Logo size="2.5rem" />
            </SvgWrapper>
            <h2>Ørkenvandring</h2>
            <SvgWrapper>
                <SkipMove size="2.5rem" skipMove={skipMove} />
                <NextShuffle size="2.5rem" reshuffle={reshuffle} />
            </SvgWrapper>
        </HeaderWrapper>
    )
}